@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.home-wrapper {
  align-items: center;
  background-color: #222;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form-wrapper {
  align-items: center;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 160px 80px;
}

.login-form {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: Roboto , sans-serif;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.login-form img {
  height: 60px;
  margin-bottom: 40px;
}

.login-form button {
  border: none;
  color: rgb(18, 18, 18);
  margin: 1.5px;
  padding: 5px;
  width: 200px;
}

.login-form input {
  border: none;
  border-bottom: 1px solid black;
  margin: 4px;
  padding: 5px;
  width: 200px;
}

.login-form input:hover {
  border-bottom: 2px solid black;
}

.active-login-button:enabled {
  background-color: rgb(251, 193, 5);
}

.active-login-button:disabled {
  background-color: rgb(244, 244, 244);
  cursor: not-allowed;
}

.active-login-button:enabled:hover {
  background-color: rgb(255, 231, 153);
  border-color: rgb(255, 231, 153);
  cursor: pointer;
}

.settings-button {
  cursor: pointer;
}

.questions-wrapper {
  align-items: center;
  background-color: white;
  border-radius: 0 0 16px 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 80px 60px 140px;
}

.questions-wrapper img {
  height: 35px;
}

.question-container {
  align-self: flex-start;
}

.question-category {
  background-color: black;
  border-radius: 6px;
  color: white;
  margin-left: 12px;
  max-width: 60%;
  min-width: 30%;
  padding: 4px 12px;
}

.question-text {
  color: rgb(18, 18, 18);
  font-size: 28px;
  margin-top: 20px;
  padding: 4px 12px;
}

.question-category-container {
  align-items: center;
  align-self: flex-start;
  display: flex;
  justify-content: space-between;
}

.timer-wrapper {
  align-items: center;
  display: flex;
}

.time {
  font-family: Share Tech Mono , monospace;
  font-size: 25px;
}

.answers-wrapper {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
  margin: 30px 0;
  padding: 0 12px;
  width: 100%;
}

.answers-wrapper button {
  padding: 10px 0;
  width: 48%;
}

.answers-wrapper button:enabled {
  background-color: black;
  border-color: black;
  color: white;
}

.answers-wrapper button:hover:disabled {
  cursor: not-allowed;
}

.answers-wrapper button:hover:enabled {
  background-color: rgba(0, 0, 0, 0.87);
  cursor: pointer;
}

.correct-answer:disabled {
  background-color: rgb(6, 240, 15);
  border: 3px solid rgb(6, 240, 15);
  color: white;
}

.wrong-answer:disabled {
  background-color: red;
  border: 3px solid red;
  color: white;
}

.next-button {
  align-self: flex-start;
  background-color: rgb(251, 193, 5);
  border: none;
  color: rgb(18, 18, 18);
  margin: 0 12px;
  padding: 12px 0;
  width: 94%;
}

.next-button:hover {
  background-color: rgb(255, 231, 153);
  border-color: rgb(255, 231, 153);
  cursor: pointer;
}

header {
  background-color: white;
  border-radius: 16px 16px 0 0;
  color: black;
  height: 110px;
  width: 50vw;
}

header img {
  border-radius: 8px;
  height: 80px;
  margin: 6px;
}

.player-infos-wrapper {
  align-items: center;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  line-height: 25px;
  margin-left: 50px;
  padding-top: 15px;
}

.player-name-title, .score-title {
  background-color: black;
  border-radius: 6px 0 0 6px;
  color: white;
  font-weight: 700;
  padding: 4px 12px;
}

.player-name, .score {
  background-color: rgb(251, 193, 5);
  border-radius: 0 6px 6px 0;
  color: rgb(18, 18, 18);
  min-width: 100px;
  padding: 4px 10px;
  text-align: center;
}

.player-infos-container {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.player-name-wrapper {
  display: flex;
}

.score-wrapper {
  display: flex;
}

.feedback-container {
  background-color: white;
  border-radius: 0 0 16px 16px;
  padding: 40px;
}

.feedback-container section {
  margin-left: 13px;
  margin-top: 40px;
}

.feedback-container h1 {
  font-size: 36px;
  margin-bottom: 10px;
}

.feedback-container p {
  line-height: 25px;
}

.ranking-button {
  background-color: black;
  border: none;
  color: white;
  margin-right: 10px;
  margin-top: 80px;
  padding: 14px 18px;
}

.play-again-button {
  background-color: rgb(251, 193, 5);
  border: none;
  color: rgb(18, 18, 18);
  padding: 14px 18px;
}

.play-again-button:hover {
  background-color: rgb(255, 231, 153);
  cursor: pointer;
}


.ranking-button:hover {
  background-color: rgba(0, 0, 0, 0.87);
  cursor: pointer;
}

.ranking-container {
  background-color: white;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  grid-gap: 6px;
  gap: 6px;
  height: 100%;
  margin: 60px 0;
  overflow: auto;
  padding: 60px;
  width: 100%;
}

.ranking-container::-webkit-scrollbar {
  background-color: transparent;
  border-radius: 16px;
}

.ranking-container::-webkit-scrollbar-thumb {
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.158), transparent);
  border-radius: 20px;
}

.ranking-container h1 {
  background-color: black;
  color: white;
  font-size: 36px;
  margin-bottom: 30px;
  padding: 6px 0;
  text-align: center;
  width: 100%;
}

.ranking-container button {
  background-color: rgb(251, 193, 5);
  border: none;
  color: rgb(18, 18, 18);
  margin-top: 20px;
  padding: 16px;
  width: 100%;
}

.ranking-container button:hover {
  background-color: rgb(255, 231, 153);
  border-color: rgb(255, 231, 153);
  cursor: pointer;
  font-weight: 700;
}

.ranking-infos {
  align-items: center;
  display: flex;
  line-height: 40px;
}

.ranking-infos img {
  border-radius: 8px;
  height: 80px;
  margin-right: 12px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #222;
  display: flex;
  justify-content: center;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  width: 50vw;
}

